<template>
    <a-spin :spinning="loading">
      <a-form-model
        ref="form"
        :model="entity"
        :rules="rules"
        autocomplete="off"
        layout="vertical"
      >
        <div class="card">
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="班次名称" prop="name" ref="name">
                <a-input
                  v-model="entity.name"
                  placeholder="请输入班次名称"
                  name="name"
                  @change="inChange"
                ></a-input>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="开始时间" prop="startTime">
                <a-time-picker
                  v-model="entity.startTime"
                  placeholder="请选择开始时间"
                  format="HH:mm"
                  name="startTime"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="结束时间" prop="endTime">
                <a-time-picker
                  v-model="entity.endTime"
                  placeholder="请选择结束时间"
                  format="HH:mm"
                  name="endTime"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="描述" prop="description">
                <a-textarea
                  v-model:value="entity.description"
                  placeholder="请输入描述"
                  :rows="4"
                  :max-length="1000"
                  name="description"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
        <g-collapse headerName="其他信息">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="6">
                <!-- 创建人 -->
                <a-form-model-item label="创建人">
                  <span>admin</span>
                </a-form-model-item>
              </a-col>
              <a-col :sm="6">
                <!-- 最近修改时间 -->
                <a-form-model-item label="修改时间">
                  <span> 2025-04-20 15:00</span>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </a-form-model>
    </a-spin>
  </template>
  
  <script>
  import { GNdoPageEditBase } from "@/utils";
  import moment from 'moment'; // 导入 moment
  
  export default {
    name: "CreateOrEditShift",
    mixins: [GNdoPageEditBase],
    data() {
      return {
        entity: {
          name: "",
          startTime: null, // 初始化为 null
          endTime: null,   // 初始化为 null
          description: "",
        },
        rules: {
          name: [{ required: true, message: "班次名称不能为空", trigger: "change" }],
          startTime: [
            { required: true, message: "开始时间不能为空", trigger: "change" },
          ],
          endTime: [
            { required: true, message: "结束时间不能为空", trigger: "change" },
          ],
        },
      };
    },
  
    mounted() {},
  
    methods: {
      // 初始化
      init() {
        this.loading = true;
        if (this.id) {
          // 模拟请求
          setTimeout(() => {
            this.loading = false;
            this.entity = {
              name: "早班",
              startTime: moment('08:00', 'HH:mm'), // 使用 moment 格式化
              endTime: moment('16:00', 'HH:mm'),   // 使用 moment 格式化
              description: "早班工作时间",
            };
          }, 500);
        } else {
          this.reset();
          this.loading = false;
        }
      },
      resetFormData() {
        // 重置表单绑定的数据源
        this.entity = {
          name: "",
          startTime: null, // 重置为 null
          endTime: null,   // 重置为 null
          description: "",
        };
      },
      async save() {
        // 保存 校验
        let res = await this.checkError();
        if (res) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.saveSuccess(this.entity.name);
            this.handleSaveEndBackView(this.id);
            this.refreshParent({
              reset: null,
            });
          });
        }
      },
  
      inChange(e) {
        console.log(e.target.value);
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>